// ====================================
// Header's style
// ====================================

/*
* TABLE OF CONTENTS
*
* topbar component
* logo component
* header component
*/

// > topbar component
@include Block(topbar) {
    @include clearfix;
    height: 30px;
    background-color: #f5f5f5;
    border-bottom: 1px solid #ddd;
    font-family: $roboto;
    @include font(12, 30);
    time {
        float: left;
    }
    .info {
        float: right;
    }
    .link {
        float: left;
        >*:after {
            content: "|";
            display: inline-block;
            padding: 0 5px;
            color: #999;
        }
    }
    .social {
        float: left;
    }
    >.l-container {
        background-color: #f5f5f5;
        border-bottom: 1px solid #ddd;
    }
}

// > logo component
@include Block(logo) {
    position: absolute;
    z-index: 10;
    width: 215px;
    height: 59px;
    background: url(../img/logo.png) 0 0 no-repeat;
    background-size: 215px 59px;
}

// > header component
@include Block(header) {
    @include clearfix;
    position: relative;
    z-index: 10;
    font-weight: $normal;
    background-color: #fff;
    .logo {
        top: 31px;
    }
    .fyi {
        position: absolute;
        top: 10px;
        right: 0;
        width: 728px;
        height: 90px;
    }
    > .l-container {
        height: 110px;
        position: relative;
    }
    .search {
        position: relative;
        border: 1px solid #ddd;
        padding: 2px 5px;
        height: 25px;
        background: #fff;
        margin-top: 2px;
        float: right;
        input {
            padding: 0;
            width: 160px;
            height: 20px;
            border: none;
            vertical-align: top;
        }
        button {
            width: 20px;
            height: 20px;
            border: none;
            background: none;
            i {
                vertical-align: top;
            }
        }
    }
}