// ====================================
// All region in project
// ==================================== 
// Description : heading + button + region style 

/*
* TABLE OF CONTENTS
*
* HEADER
*    -> sidebar's box header
*    -> go to top button
*/


/* > HEADER */

// >> sidebar's box header
.block__header {
    border-top: 1px solid $link-hover-color;
    @include clearfix;
    margin-bottom: 10px;
    padding-top: 5px;
    h2 {
        float: left;
        a {
            color: $link-hover-color;
            @include font(20, 36, $bold, normal, $noto-serif);
            height: 30px;
            display: block;
            text-transform: uppercase;
        }
    }
    h3 {
        a {
            color: #555;
            @include font(12, 40, $normal, normal, $noto-serif);
            text-transform: uppercase;
            height: 30px;
            display: block;
        }
        float: left;
        margin-left: 30px;
        @include first-x;
    }
}

.mini__header {
    a {
        display: block;
        text-transform: uppercase;
        @include font(14, 20, $bold, normal, $noto-serif);
        color: $link-hover-color;
        border-bottom: 1px solid $link-hover-color;
        line-height: 30px;
    }
}

// >> go to top button
#go-top {
    cursor: pointer;
    display: none;
    position: fixed;
    bottom: 15px;
    right: 15px;
    width: 40px;
    height: 40px;
    background-color: rgba(255, 255, 255, .7);
    border: 1px solid #eee;
    outline-color: red;
    text-align: center;
    z-index: 90;
    i {
        width: 15px;
        height: 15px;
    }
    a {
        vertical-align: middle;
        margin-top: 5px;
        display: block;
    }
}

.topics {
    @include clearfix();
    margin-bottom: 10px;
    > span {
        float: left;
        font-size: 20px;
        font-weight: bold;
        text-transform: uppercase;
        display: block;
        color: $link-hover-color;
    }
    li {
        display: inline-block;
        float: left;
        font-size: 14px;
        a {
            display: block;
            padding-left: 26px;
            background: url(../img/sprites.png) left -478px top 5px no-repeat;
            background-size: 500px 200px;
            margin-bottom: 3px;
        }
    }
    .event-list {
        padding-top: 5px;
        margin-left: 120px;
    }
}

.video-block-top {
    width: 300px;
    .story {
        height: 270px;
        background-color: $link-hover-color;
        margin-bottom: 10px;
        .story__thumb {
            margin-bottom: 10px;
            &:after {
                position: absolute;
                top: calc(50% - 25px);
                left: calc(50% - 25px);
                content: " ";
                width: 50px;
                height: 50px;
                background: url(../img/sprites.png) 0 -50px no-repeat;
                background-size: 550px 200px;
                display: inline-block;
            }
        }
        a {
            color: #fff;
            &:hover {
                text-decoration: underline;
            }
        }
        .story__heading {
            font-weight: bold;
            padding: 0 10px;
        }
        .story__meta {
            padding: 0 10px;
            color: #fff;
            time {
                color: #fff;
            }
        }
    }
}
