/*
* TABLE OF CONTENTS
*
* soccer landing : at homepage
* soccer schedule : category
* modal
*/

// > soccer landing : at homepage
.soccer-landing {
    position: relative;
    margin-bottom: 0 !important;
    a {
        cursor: pointer;
    }
    .select-league {
        position: absolute;
        width: 145px;
        height: 30px;
        border-radius: 3px;
        border: 0;
        background-color: #fff;
        position: absolute;
        top: 40px;
        left: 12px;
        font-weight: bold;
        font-size: 12px;
        font-size: 1.2rem;
        font-family: Baomoi, arial, sans-serif;
    }
    .wrap {
        height: 180px;
        overflow: hidden;
        border-left: 1px solid #e6e6e6;
        border-right: 1px solid #e6e6e6;
        border-bottom: 1px solid #e6e6e6;
        &::-webkit-scrollbar-button {
            width: 0px;
            height: 0px;
        }
        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
        }
        &::-webkit-scrollbar-thumb {
            background: rgba(0, 0, 0, .3);
            border: 0px none #000;
            border-radius: 50px;
        }
    }
    table {
        width: 100%;
    }
    td {
        height: 30px;
        line-height: 30px;
        text-align: center;
    }
    .title {
        background-color: #fff !important;
        > td:first-child{
            width: 70%;
        }
        > td:nth-child(2){
            width: 15%;
        }
        > td:nth-child(3){
            width: 15%;
        }
    }
    .group {
        td {
            background-color: #FFE195;
            color: #555;
            text-align: left !important;
            padding-left: 10px;
        }
    }
    .pos {
        td:nth-child(1) {
            width: 10%;
            text-align: center;
        }
        td:nth-child(2) {
            width: 60%;
            text-align: left;
        }
        td:nth-child(3) {
            width: 15%;
            text-align: center;
        }
        td:nth-child(4) {
            width: 15%;
            text-align: center;
        }
    }
    .top {
        background-color: #EFF6D3;
    }
    tr:nth-child(odd) {
        background-color: #eee;
    }
    .expand {
        display: block;
        font-size: 12px;
        text-transform: uppercase;
        text-align: right;
        margin-top: 5px;
        &:hover {
            color: #15a1b1;
        }
    }
}

@supports (overflow:-webkit-marquee) and (justify-content:inherit) {
    .soccer-landing {
        .select-league {
            height: 25px;
            line-height: 25px;
            border: 1px solid #ddd;
            left: 0;
        }
    }
}
/* ---- Firefox ---- */
@supports (-moz-appearance:none) and (writing-mode:horizontal-tb) {
    .soccer-landing {
        .select-league {
            height: 25px;
            line-height: 25px;
            border: 1px solid #ddd;
            left: 0;
        }
    }
}


// > soccer schedule : category
.soccer-schedule {
    @include clearfix;
    font-size: 14px;
    font-size: 1.4rem;
    position: relative;
    border-bottom: 1px solid #eee;
    .title-region {
        @include clearfix;
        border-bottom: 4px solid #ABD120;
        width: 100%;
        line-height: 35px;
        display: inline-block;
        text-transform: uppercase;
        font-size: 18px;
        font-size: 1.8rem;
        font-weight: bold;
        line-height: 35px;
        padding-right: 17px;
        padding: 0 13px 0 13px;
        display: inline-block;
    }
    .calendar {
        @include clearfix;
        text-align: center;
        padding: 10px;
    }
    .contents {
        margin-top: 10px;
        overflow: hidden;
    }
    .timeline {
        vertical-align: middle;
        display: inline-block;
        a {
            padding: 5px 7px;
            display: block;
            color: #000;
            cursor: pointer;
            &:hover{
                color: $link-hover-color;
            }
        }
        li {
            display: inline-block;
            text-align: center;
            white-space: nowrap;
        }
        .active {
            background-color: #ABD120;
            a {
                padding: 5px 14px;
                color: #fff;
            }
        }
    }
    .prev,
    .next {
        display: inline-block;
        width: 9px;
        height: 14px;
        text-indent: -999em;
        background-repeat: no-repeat;
        padding: 12px;
        vertical-align: middle;
        background: none;
        border: none;
        background-image: url("../img/soccer.png");
        background-size: 300px 150px;
        margin-top: 2px;
    }
    .prev {
        background-position: 7px 5px;
        float: left;
        margin-left: -5px;
    }
    .next {
        background-position: -42px 5px;
        float: right;
        margin-right: -5px;
    }
    .select-league {
        width: 200px;
        border-radius: 3px;
        border: 1px solid #ddd;
        background-color: #fff;
        position: absolute;
        top: 5px;
        right: 10px;
        font-size: 12px;
        font-size: 1.2rem;
        font-weight: bold;
        height: 24px;
        font-family: "baomoi", Arial, "Helvetica Neue", Helvetica, sans-serif;

    }
    .soccer-table {
        width: 100%;
        border: 1px solid #ddd;
        td {
            height: 30px;
            line-height: 30px;
            padding: 0;
        }
        .league-name {
            td {
                width: 100% !important;
                background-color: rgba(171, 209, 32, 0.44);
                color: #555;
                text-align: left !important;
                padding-left: 10px;
            }
        }
        a {
            cursor: pointer;
        }
        tr {
            td {
                text-align: center;
            }
            td:nth-child(1) {
                width: 10%;
                text-align: left;
                padding-left: 10px;
            }
            td:nth-child(2) {
                width: 35%;
                text-align: right;
                padding-right: 10px;
            }
            td:nth-child(3) {
                width: 5%;
            }
            td:nth-child(4) {
                width: 10%;
            }
            td:nth-child(5) {
                width: 5%;
            }
            td:nth-child(6) {
                padding-left: 10px;
                width: 35%;
                text-align: left;
            }
        }
        tr:nth-child(odd) {
            background-color: #eee;
        }
    }
    .expand {
        display: block;
        font-size: 12px;
        text-transform: uppercase;
        text-align: right;
        margin-top: 3px;
        &:hover {
            color: #15a1b1;
        }
    }
}

