// ====================================
// Layout's style
// ====================================

/*
* TABLE OF CONTENTS
*
* wrapper - wrap entire pages
* Container width
* homepage
* category
* other page
* article
* style fix modernizr
*/


/* > wrapper - wrap entire pages  */

.l-wrapper {
    @include clearfix;
}


/* > Container width */

@include Block(l-container) {
    margin: 0 auto;
    @include clearfix;
    position: relative;
    width: 980px;
    @include Element(main) {
        width: 665px;
        float: left;
    }
    @include Element(sideBar) {
        width: 300px;
        float: right;
    }
}



@include Block(l-editor) {
    @include clearfix;
    margin-bottom: 10px;
    @include Element(col) {
        float: left;
        width: 215px;
        margin-left: 10px;
        @include first-x;
    }
}

.abc-column {
    @include clearfix;
}

.ab-column {
    float: left;
    width: 665px;
}

.c-column {
    width: 300px;
    float: right;
    .fyi {
        margin-bottom: 20px;
    }
    .hot-news,
    .latest-news,
    {
        margin-bottom: 20px;
    }
}

// > homepage 
.idx-content {
    @include clearfix;
    font-size: 0;
}

// > category 
.cate-content {
    @include clearfix;
    font-size: 0;
}

// > other page
.topics-content,
.original-content,
.cluster-content,
.tag-content,
.search-content {
    @include clearfix;
    font-size: 0;
}

// > article 
.article {}

// > style fix modernizr
//
.no-js {
    #header,
    .navigation {
        overflow: hidden;
    }
}
