// > footer style
.footer {
    .logo {
        position: absolute;
        right: 0;
        top: 10px;
        background: none;
        width: initial;
        img{
            width: auto;
            height: 60px;
        }
    }
    @include Element(copyRight) {
        @include clearfix;
        width: 600px;
        float: left;
        color: #777;
        font-size: 14px;
        font-size: 1.4rem;
        line-height: 22px;
        font-family: $roboto;
    }
}
