// ====================================
// Customize breadcrumbs
// ====================================

/*
* TABLE OF CONTENTS
*
* pagination + pager
*/

// > pagination + pager
.breadcrumbs {
    @include clearfix;
    margin-bottom: 10px;
    .cate {
        float: left;
        a {
            color: $link-hover-color;
            @include font(20, 36, $bold, normal, $noto-serif);
            height: 30px;
            display: block;
        }
    }
    .sub {
        float: left;
        margin-left: 30px;
        @include first-x;
        a {
            color: #555;
            @include font(12, 40, $normal, normal, $noto-serif);
            text-transform: uppercase;
            height: 30px;
            display: block;
        }
    }
}
