// ====================================
// Story's style
// ====================================

/*
* TABLE OF CONTENTS
*
* STORY
*    -> left thumb
* base story
* top-story
* no image in article
* editor choice
* normal block
* blue block
* blue block
* personal event
* list article
* latest-box
* hotest-box
* slider-video
* epaper
*/


/* > STORY */

// >> left thumb
@mixin left-story {
    figure {
        @include runaround(left, 3, 10, 0, 0);
    }
}

// > base story
.story {
    @include clearfix;
    position: relative;
    @include Element(thumb) {
        position: relative;
        max-width: 100%;
        img {
            width: 100%;
            height: 100%;
        }
    }
    @include Element(heading) {
        overflow: hidden;
        @include font(16, 21);
        margin-bottom: 5px;
    }
    @include Element(meta) {
        font-size: 12px;
        font-size: 1.2rem;
        font-family: $roboto;
        margin-bottom: 5px;
        a {
            margin-right: 10px;
        }
    }
    @include Element(tag) {
        font-family: $roboto-cnd;
        @include font(11, 22);
        height: 20px;
        overflow: hidden;
        background-color: $link-active-color;
        margin-right: 0;
        color: #fff;
        display: inline-block;
        position: absolute;
        text-transform: uppercase;
        padding: 0 5px;
        margin-right: 0;
        top: 0;
        left: 0;
        &:hover {
            text-decoration: underline;
            color: #fff;
        }
    }
    @include Element(time) {
        color: #999;
        display: inline-block;
        margin-right: 10px;
    }
    @include Element(summary) {
        @include font(13, 19);
        color: #777;
        font-family: $roboto;
        margin-bottom: 5px;
    }
    @include Element(refer) {
        @include font(11, 15);
        font-family: $roboto;
        a:before {
            content: "ꔷ";
            width: 4px;
            height: 6px;
            margin-right: 5px;
            display: inline-block;
            color: #E64C3C;
        }
        li {
            margin-bottom: 10px;
        }
    }
}

// --------------
// > top-story
.topStory {
    background-color: #F5F5F5;
    height: 270px;
    overflow: hidden;
    padding: 5px;
    margin-bottom: 10px;
    .story__thumb {
        @include runaround(right, -5, -5, 0, 10);
        width: 440px;
        height: 270px;
    }
    .story__heading {
        @include font(28, 34, $bold);
        margin-bottom: 10px;
    }
    .story__tag {
        top: 244px;
        left: 225px;
    }
}

// > no image in article
.no-fig {
    .story__thumb {
        a {
            display: table;
            width: 100%;
            height: 100%;
            &:before {
                content: 'Không có ảnh';
                display: block;
                background: #f7f7f7;
                text-align: center;
                text-transform: uppercase;
                color: #ccc;
                width: 100%;
                height: 100%;
                display: table-cell;
                vertical-align: middle;
                font-size: 12px;
                font-size: 1.2rem;
            }
        }
        img {
            display: none;
        }
    }
}

// --------------
// > editor choice
.editor-choice {
    .story {
        background-color: #f5f5f5;
        border-bottom: 1px solid #ddd;
        height: 70px;
        margin-bottom: 10px;
        overflow: hidden;
        padding: 7px 5px 0px;
        &__heading {
            @include font(14, 19);
        }
        &__thumb,
        &__tag,
        &__meta,
        &__summary {
            display: none;
        }
    }
    .is-first,
    .is-second {
        height: 310px;
        .story__thumb {
            display: block;
            width: 215px;
            max-width: 215px;
            height: 130px;
            margin: -5px;
            margin-bottom: 5px;
        }
        .story__heading {
            @include font(16, 21, $bold);
            margin-bottom: 10px;
        }
        .story__tag {
            display: block;
            top: 107px;
        }
    }
    .is-second {
        height: 230px;
    }
}

// --------------
// > normal block
.normal-block {
    @include clearfix;
    margin-bottom: 10px;
    .block__main {
        width: 286px;
        float: left;
        padding-right: 10px;
        border-right: 1px solid $border;
        margin-right: 10px;
        .story {
            &__heading {
                @include font(20, 26, $bold);
            }
            &__thumb {
                width: 275px;
                height: 170px;
                margin-bottom: 7px;
            }
            &__tag {
                top: 145px;
            }
        }
    }
    .block__sub {
        float: left;
        width: 369px;
        margin-top: -3px;
        .story {
            @include clearfix;
            @include left-story;
            border-top: 1px solid $border;
            margin-top: 10px;
            padding-top: 7px;
            &__heading {
                @include font(16, 22);
            }
            &__thumb {
                width: 118px;
                height: 73px;
            }
            @include first-y;
        }
    }
}

// --------------
// > blue block
.blue-block {
    @include clearfix;
    margin-bottom: 30px;
    .story {
        background-color: $link-hover-color;
    }
    .block__main {
        width: 491px;
        float: left;
        padding-right: 10px;
        border-right: 1px solid $border;
        margin-right: 10px;
        .story {
            width: 480px;
            height: 410px;
            &__thumb {
                width: 480px;
                height: 295px;
                margin-bottom: 7px;
            }
            &__heading {
                @include font(28, 34, $bold);
                color: #fff;
                padding: 0 5px;
            }
            &__tag {
                top: 270px;
            }
        }
    }
    .block__sub {
        float: left;
        margin-right: 9px;
        padding-right: 9px;
        border-right: 1px solid $border;
        .story {
            @include clearfix;
            width: 230px;
            height: 410px;
            padding: 10px;
            border-left: 1px solid $border;
            float: left;
            &__heading {
                @include font(16, 22);
                color: #fff;
            }
            &__thumb {
                width: 230px;
                max-width: 230px;
                height: 140px;
                margin-left: -10px;
                margin-top: -10px;
                margin-bottom: 7px;
            }
            &__summary {
                color: #fff;
                opacity: .7;
                margin-bottom: 15px;
            }
            &__refer {
                a:before {
                    display: none;
                }
                img {
                    width: 80px;
                    height: 50px;
                    margin-right: 10px;
                    float: left;
                    margin-top: 3px;
                }
                span {
                    display: block;
                    margin-left: 90px;
                    color: #fff;
                    @include font(14, 19, $normal, normal, $roboto-cnd);
                }
            }
        }
    }
    .block__sub:last-child {
        @include last-x;
    }
}

// --------------
// > blue block
.top-ten {
    float: left;
    @include clearfix;
    width: 665px;
    background: #fff url(../img/top-ten-bg.png) 0 0 no-repeat;
    ul {
        float: left;
        width: 270px;
        padding-top: 5px;
        margin-left: 90px;
        li {}
        a {
            padding-top: 5px;
            padding-bottom: 25px;
            display: block;
            @include font(16, 21, $bold, normal, $roboto-cnd);
            height: 72px;
        }
        @include first-x;
    }
}

// --------------
// > personal event
.personal-events {
    @include clearfix;
    margin-bottom: 30px;
    .story {
        margin-left: 20px;
        @include first-x;
        float: left;
        width: 230px;
        &__heading {
            @include font(16, 22, $bold);
        }
        &__thumb {
            width: 230px;
            height: 140px;
            margin-bottom: 7px;
        }
        &__tag {
            top: 115px;
        }
    }
}

// --------------
// > list article
.list-region {
    border-top: 1px solid $link-hover-color;
    .story {
        @include left-story;
        margin-top: 17px;
        &__heading {
            @include font(16, 22, $bold);
        }
        &__thumb {
            width: 162px;
            height: 100px;
        }
        &__tag {
            top: 115px;
        }
    }
}

// --------------
// > latest-box
.latest-box {
    margin-bottom: 10px;
    .story {
        background-color: #F5F5F5;
        @include left-story;
        margin-top: 10px;
        height: 70px;
        overflow: hidden;
        &__heading {
            @include font(14, 20);
            padding: 5px;
            padding-left: 0;
        }
        &__thumb {
            width: 115px;
            height: 70px;
            margin-top: 0 !important;
        }
    }
    @include first-y;
}

// --------------
// > hotest-box
.hotest-box {
    margin-bottom: 10px;
    .hotest__body {
        margin: 0 -5px;
        font-size: 0;
        padding-top: 10px;
    }
    .story {
        margin: 0 5px 5px;
        vertical-align: top;
        width: 145px;
        display: inline-block;
        &__heading {
            @include font(14, 20);
        }
        &__thumb {
            width: 145px;
            height: 90px;
            margin-bottom: 5px;
        }
    }
}

// --------------
// > slider-video
.slider-video {
    max-height: 460px;
    overflow: hidden;
    .block__main {
        width: 665px;
        margin-left: 15%;
    }
    .bx-wrapper {
        position: relative;
    }
    .bx-next,
    .bx-prev {
        width: 24px;
        height: 24px;
        display: block;
        background-image: url(../img/sprites.png);
        background-size: 550px 200px;
        background-repeat: no-repeat;
        text-indent: -999em;
        position: absolute;
        top: 190px;
        &:hover {
            opacity: .7;
        }
    }
    .bx-prev {
        background-position: 0px -100px;
        left: 110px;
    }
    .bx-next {
        background-position: -50px -100px;
        right: 130px;
    }
    .bx-viewport {
        background-color: #000;
    }
    .story {
        width: 665px;
        height: 410px;
        position: relative;
        opacity: .5;
        transition: all .2s;
        &__heading {
            @include font(23, 34, $bold);
            color: #fff;
            position: absolute;
            bottom: 10px;
            right: 0;
            left: 0;
            height: 68px;
            overflow: hidden;
            width: 100%;
            padding: 0px 20px 0px 100px;
            margin-bottom: 0;
            background-image: url(../img/sprites.png);
            background-size: 550px 200px;
            background-position: -175px 0px;
            background-repeat: no-repeat;
            display: inline-block;
        }
        &__thumb {
            margin-bottom: 0;
            &:after {
                content: '';
                position: absolute;
                background: url(../img/mask.png) 0 bottom repeat-x;
                width: 665px;
                height: 410px;
                top: 0;
            }
        }
        &:hover {
            .story__heading {
                opacity: .8;
            }
        }
    }
    .is-active {
        opacity: 1;
    }
}

// > epaper
.epaper {
    .block__header {
        margin-bottom: 20px;
    }
    &__content {
        position: relative;
        height: 210px;
        overflow: hidden;
        .story__thumb {
            width: 220px;
            height: 150px;
            margin-bottom: 5px;
        }
        .story__heading {
            @include font(16, 21, $bold);
            text-align: center;
        }
    }
    .bx-next,
    .bx-prev {
        width: 24px;
        height: 24px;
        display: block;
        background-image: url(../img/sprites.png);
        background-size: 550px 200px;
        background-repeat: no-repeat;
        text-indent: -999em;
        position: absolute;
        top: 65px;
        &:hover {
            opacity: .7;
        }
    }
    .bx-prev {
        background-position: -200px -100px;
        left: 10px;
    }
    .bx-next {
        background-position: -250px -100px;
        right: 10px;
    }
}

// > editons box
.editions-box {
    margin: 0 -5px;
    .story {
        width: 234px;
        margin: 0 5px;
        margin-bottom: 20px;
        vertical-align: top;
        display: inline-block;
        &__thumb {
            margin-bottom: 5px;
        }
        &__heading {
            @include font(18, 28, $bold);
        }
    }
}

// > utilities  weather
.utilities {
    width: 300px;
    float: right;
    .weather {
        @include clearfix();
        display: block;
        padding: 10px;
        border: 1px solid #ddd;
        background-color: #f5f5f5;
        margin-bottom: 10px;
        height: 100px;
    }
    .weather__info {
        width: 140px;
        float: left;
    }
    .note:first-child {
        font-size: 40px;
        color: $link-hover-color;
    }
    .note:last-child {
        font-size: 14px;
    }
    select {
        float: right;
        margin-top: 14px;
        width: 120px;
    }
    .gold-usd-price {
        @include clearfix();
        display: block;
        padding: 10px;
        background-color: #f5f5f5;
        border: 1px solid #ddd;
        margin-bottom: 10px;
        height: 100px;
        .name {
            font-weight: bold;
            font-size: 25px;
            margin-bottom: 10px;
            color: $link-hover-color;
        }
        .gup__content {
            font-size: 18px;
            span {
                display: block;
                float: left;
                &:last-child {
                    float: right;
                }
            }
        }
    }
}

.hilitop.normal-block .block__sub{
    .story {
        margin-top: 12px;
        padding-top: 9px;
    }
    .story:first-child{
        margin-top: 0;
        padding-top: 0;
    }
}
